export default {
	name: 'shoproMiniCard',
	components: {},
	data() {
		return {
			routerTo: this.$Router,
			orderStatus: {
				seckill: '/static/imgs/seckill_tag.png',
				groupon: '/static/imgs/groupon_tag.png'
			}
		};
	},
	props: {
		detail: {},
		sku: {},
		type: ''
	},
	computed: {
		goodsId() {
			return this.detail.goods_id;
		}
	},
	created() {},
	methods: {
		// 路由跳转
		jump(path, parmas) {
			this.$Router.push({
				path: path,
				query: parmas
			});
		}
	}
};
